// Woff Generated Fonts Using
@mixin font($font_name, $file_name, $weight, $style) {
  @font-face {
    font-family: $font_name;
    font-display: swap;
    src: url("../webfonts/#{$file_name}.woff") format("woff"), url("../webfonts/#{$file_name}.woff2") format("woff2");
    font-weight: #{$weight};
    font-style: #{$style};
  }
}

// margin-auto
@mixin margin--auto {
  margin: {
    left: auto;
    right: auto;
  }
}

// Placeholders
@mixin input-placeholder {
  &.placeholder {
    @content;
  }
  &:-moz-placeholder {
    @content;
  }
  &::-moz-placeholder {
    @content;
  }
  &:-ms-input-placeholder {
    @content;
  }
  &::-webkit-input-placeholder {
    @content;
  }
}

// Gradients
@mixin gen-light-gray-gradient {
  background: rgba(209, 212, 216, 1);
  background: -moz-linear-gradient(135deg, $gen-grad-col1 0%, $gen-grad-col2 100%);
  background: -webkit-linear-gradient(135deg, $gen-grad-col1 0%, $gen-grad-col2 100%);
  background: linear-gradient(135deg, $gen-grad-col1 0%, $gen-grad-col2 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ebedef",endColorstr="#d1d4d8",GradientType=1);
}

