// Simple Grayscale Mixin
@mixin grayscale_element($value) {
  -webkit-filter: #{"grayscale(#{$value})"};
  -moz-filter: #{"grayscale(#{$value})"};
  filter: #{"grayscale(#{$value})"};
}

$template-width: 1440;

// Adaptive Font Size
@mixin adaptive-font-size($deskSize, $mobSize) {
  $diffSize: $deskSize - $mobSize;
  $diffMobSize: $diffSize + $diffSize * 0.7;
  @include media-breakpoint-down(sm) {
    font-size: calc(#{$mobSize + px} + #{$diffMobSize} * ((100vw - 320px) / #{$template-width}));
  }
  @include media-breakpoint-up(sm) {
    font-size: calc(#{$mobSize + px} + #{$diffSize} * (100vw / #{$template-width}));
  }
  @media (min-width: #{$template-width + px}) {
    font-size: #{$deskSize + px};
  }
}

//Adaptive Line Height
@mixin adaptive-line-height($deskSize, $mobSize) {
  $diffSize: $deskSize - $mobSize;
  $diffMobSize: $diffSize + $diffSize * 0.7;
  @include media-breakpoint-down(sm) {
    line-height: calc(#{$mobSize + px} + #{$diffMobSize} * ((100vw - 320px) / #{$template-width}));
  }
  @include media-breakpoint-up(sm) {
    line-height: calc(#{$mobSize + px} + #{$diffSize} * (100vw / #{$template-width}));
  }
  @media (min-width: #{$template-width + px}) {
    line-height: #{$deskSize + px};
  }
}
