.page-header {
  position: relative;
  overflow-x: hidden;
  &:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background-color: rgb(245, 245, 246);
    z-index: -1;
  }
  .row:first-child {
    > div {
      padding-left: 25px;
      padding-right: 25px;
      &:first-child {
        @include media-breakpoint-up(md) {
          padding-left: 0;
          padding-right: 0;
          display: flex;
          flex-wrap: wrap;
          align-items: center;
          justify-content: space-between;
        }
        > div {
          padding-left: 0;
          padding-right: 0;
        }
      }
      @include media-breakpoint-up(md) {
        padding-left: 50px;
        padding-right: 50px;
      }
    }
  }
  .header-navbar {
    @include media-breakpoint-down(sm) {
      justify-content: space-between;
      display: flex;
    }
  }
  .navbar {
    padding: 0;
    > div {
      &:first-child {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        @include media-breakpoint-down(xs) {
          flex-direction: column;
          justify-content: center;
        }
      }
    }
    .navbar-brand {
      width: 227px;
      padding: 0;
      @include media-breakpoint-down(sm) {
        width: 185px;
        margin-right: auto;
      }
      @include media-breakpoint-up(lg) {
        margin-right: auto;
      }
    }
    .navbar-toggler {
      padding: 0.5rem 1rem;
      @include media-breakpoint-down(xs) {
        padding: 0.5rem 0;
      }
    }
    .btn-callback {
      font-size: 0.875rem;
      font-weight: 700;
      //text-transform: uppercase;
      letter-spacing: 1px;
      padding: 0.75rem 1.5rem;
      box-shadow: inset 0 0 2px white, 0 5px 10px 0px rgba(0, 0, 0, 0.35);
      &:hover,
      &:focus {
        box-shadow: inset 0 0 2px white, 0 3px 7px 0px rgba(0, 0, 0, 0.35);
      }
    }
    .consultation {
      text-align: center;
    }
    .btn-consultation {
      @include media-breakpoint-down(sm) {
        margin-top: 1rem;
        margin-bottom: 1rem;
      }
    }
    &.navbar-light {
      .navbar-toggler {
        border: none;
        .navbar-toggler-icon {
          background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(0,0,0, 0.87)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
        }
        &:focus {
          outline: none;
        }
      }
    }
    > [class^="col"] {
      @include media-breakpoint-down(xs) {
        padding-left: 10px;
        padding-right: 10px;
      }
    }
    .navbar-collapse {
      flex-direction: column;
      margin-top: 1rem;
      margin-bottom: 1rem;
      @include media-breakpoint-up(md) {
        margin-top: 27px;
        margin-bottom: 26px;
      }
    }
    .navbar-nav {
      display: flex;
      width: 100%;
      //justify-content: space-between;
      justify-content: space-around;
      .nav-link {
        color: black;
        padding-left: 0;
        padding-right: 0;
        @include media-breakpoint-down(xs) {
          text-align: center;
        }
        &:hover {
          color: $main-color;
        }
      }
    }
    .primary-nav {
      .nav-link {
        // font-size: 1.375rem;
        // font-weight: 700;
        // letter-spacing: 1.22px;
        svg {
          font-size: 1rem;
        }
      }
    }
    .secondary-nav {
      justify-content: flex-start;
      .nav-item {
        @include media-breakpoint-up(md) {
          margin-right: 3rem;
        }
      }
      .nav-link {
        opacity: 0.5;
        font-size: 0.8125rem;
        line-height: 0.875rem;
        font-weight: 500;
        letter-spacing: 1px;
        color: black;
        @include media-breakpoint-down(xs) {
          font-size: 1.375rem;
          letter-spacing: 1.22px;
          color: black;
          font-weight: 600;
        }
      }
    }
    .btn-link {
      white-space: nowrap;
      color: rgba(0, 0, 0, 0.67);
      span {
        color: rgba(0, 0, 0, 0.5);
      }
      &:hover {
        text-decoration: none;
        color: black;
      }
    }
    .btn-search {
      font-size: 1.375rem;
      span {
        font-size: 1rem;
      }
    }
    .nav-link {
      white-space: nowrap;
    }
  }
  .mobile-header {
    @include media-breakpoint-down(sm) {
      padding-top: 24px;
      //padding-bottom: 1rem;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
    }
  }
  .btn {
    font-size: 0.875rem;
    padding: 0.5rem 1rem;
    @include media-breakpoint-down(sm) {
      padding-left: 2rem;
      padding-right: 2rem;
    }
  }
  .phone-link {
    font-size: 20px;
    letter-spacing: 2px;
    font-weight: 600;
  }
  .header-phone {
    text-align: center;
    @include media-breakpoint-down(xs) {
      //display: none;
      padding: 0.5rem 1rem;
    }
    @include media-breakpoint-only(lg) {
      margin-top: 0.5rem;
      margin-bottom: 0.5rem;
    }
    // svg {
    //   margin-right: 0.5rem;
    // }
  }
}

.logo-link {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  .logo-img {
    @include media-breakpoint-up(md) {
      width: 227px;
    }
    @include media-breakpoint-down(md) {
      width: 185px;
    }
  }
  .slogan {
    font-size: 0.875rem;
    letter-spacing: 1px;
    line-height: 1.5rem;
    font-weight: 500;
    color: $paragraph-color;
    margin: 0;
    text-align: center;
    @include media-breakpoint-down(xs) {
      display: none;
    }
  }
}

.header-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  @include media-breakpoint-down(xs) {
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin-top: 0;
  }
  @include media-breakpoint-down(sm) {
    margin-bottom: 24px;
  }
  @include media-breakpoint-down(lg) {
    margin-top: 1rem;
    justify-content: space-around;
    width: 100%;
    margin-bottom: 1rem;
  }
  &__item {
    text-align: center;
    @include media-breakpoint-up(lg) {
      min-width: 160px;
    }
  }
  &__link:not(.btn) {
    padding: 0.5rem 1rem 0.5rem 0.5rem;
    color: black;
    &:hover {
      color: $main-color;
      text-decoration: none;
    }
  }
  .header-phone {
    text-align: center;
    @include media-breakpoint-down(xs) {
      //display: none;
      padding: 0.5rem 1rem;
    }
    // svg {
    //   margin-right: 0.5rem;
    // }
  }
  .phone-link {
    font-size: 20px;
    letter-spacing: 2px;
    font-weight: 600;
    @include media-breakpoint-up(lg) {
      //padding-right: 1.5rem;
    }
  }
  .header-social {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    @include media-breakpoint-down(xs) {
      display: none;
    }
    &__link {
      font-size: 1rem;
      padding-left: 14px;
      padding-right: 0;
      .icon {
        width: 24px;
        height: 24px;
      }
    }
  }
  .cabinet {
    @include media-breakpoint-only(lg) {
      margin-top: 0.5rem;
    }
  }
  .cabinet-link {
    font-size: 0.875rem;
    font-weight: 700;
    @include media-breakpoint-down(xs) {
      .svg-user-dims {
        display: inline-block;
      }
      span {
        display: none;
      }
    }
    .svg-user-dims {
      display: none;
      width: 24px;
      height: 24px;
      color: black;
      @include media-breakpoint-down(md) {
        display: inline-block;
      }
      @include media-breakpoint-up(sm) {
        width: 16px;
        height: 16px;
      }
    }
  }
}

#slider {
  .swiper-button-prev,
  .swiper-button-next {
    opacity: 0;
    transition: all 0.3s ease-in-out;
  }
  .swiper-button-prev {
    left: 50px;
  }
  .swiper-button-next {
    right: 50px;
  }
  .swiper-container {
    &:hover {
      .swiper-button-prev {
        left: 10px;
        opacity: 1;
      }
      .swiper-button-next {
        right: 10px;
        opacity: 1;
      }
    }
  }
  .swiper-scrollbar-drag {
    background-color: $main-color;
  }
  .slide-bg {
    img {
      width: 100%;
    }
  }
}

.header-phone {
  .svg-phone-dims {
    width: 20px;
    height: 20px;
  }
}

