.deposit-calc {
  padding-left: 0;
  padding-right: 0;
  .form {
    &__title {
      font-size: 28px;
      margin-top: 32px;
      margin-bottom: 32px;
      @include media-breakpoint-down(xs) {
        margin-top: 0.5rem;
        margin-bottom: 0.5rem;
      }
      @include media-breakpoint-up(md) {
        margin-top: 62px;
        margin-bottom: 9px;
      }
    }
  }
  .form-group {
    //margin-top: 2rem;
    &:first-child {
      margin-top: 0;
    }
  }
  .form-row {
    margin-left: 0;
    margin-right: 0;
    &.bg-gradient {
      &:nth-child(1) {
        &:before {
          right: -50vw;
          z-index: -1;
          background: linear-gradient(135deg, #fdfdfd 0%, #e4e4ec 100%);
          @include media-breakpoint-down(md) {
            left: -50vw;
          }
        }
      }
      &:nth-child(2) {
        &:before {
          right: -50vw;
          z-index: -1;
          //background: linear-gradient(135deg, #fafafb 0%, #e4e4ec 100%), linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.85) 61%, #ffffff 100%);
          background: linear-gradient(180deg, rgba(255, 255, 255, 0.0001) 21.97%, rgba(255, 255, 255, 0.851985) 69.84%, #ffffff 100%), linear-gradient(157.71deg, #fafafb 0%, #e4e4ec 100%);
          @include media-breakpoint-down(md) {
            left: -50vw;
          }
        }
      }
    }
  }
  .form-check {
    padding-left: 0;
    .form-check-label {
      position: relative;
      font-size: 1.125rem;
      display: block;
      line-height: 1.75rem;
      padding-left: 2.5rem;
      user-select: none;
      cursor: pointer;
      @include media-breakpoint-down(xs) {
        font-size: 0.875rem;
      }
      .form-check-input {
        position: absolute;
        height: 0;
        cursor: pointer;
        opacity: 0;
        height: 0;
        width: 0;
      }
      .checkmark {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        height: 28px;
        width: 28px;
        background-color: white;
        border-radius: 14px;
        border: 1px solid #ced4da;
        &:after {
          width: 7px;
          height: 14px;
          border: solid white;
          border-width: 0 3px 3px 0;
          transform: rotate(45deg);
          top: 5px;
          left: 10px;
        }
      }
      &:hover input ~ .checkmark {
        background-color: transparentize($color: $main-color, $amount: 0.75);
      }
      input:checked ~ .checkmark {
        background-color: $main-color;
        border-color: darken($color: $main-color, $amount: 5%);
        &:after {
          display: block;
        }
      }
    }
  }
  .btn[type="button"] {
    margin-bottom: 2rem;
    @include media-breakpoint-up(md) {
      margin-bottom: 3rem;
    }
    @include media-breakpoint-down(md) {
      display: block;
      width: 100%;
    }
  }
}

