.group-value {
  width: 100%;
  .form-group {
    label {
      font-weight: 700;
      color: $paragraph-color;
    }
  }
  input[type="number"] {
    -moz-appearance: textfield;
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }
  .input-type-range {
    height: 2rem;
    width: 100%;
    position: relative;
    overflow: hidden;
    margin-top: -2rem;
  }
  .input-type-range-track {
    width: inherit;
    //background-color: #b7b1b1;
    //height: 2px;
    height: 0;
    border: 1px solid #b7b1b1;
    position: absolute;
    border-radius: 1rem;
    //z-index: -1;
    top: 50%;
    transform: translateY(-50%);
    &:before,
    &:after {
      content: "";
      position: absolute;
      width: 5px;
      height: 5px;
      background-color: #b7b1b1;
      border-radius: inherit;
      top: 0;
      z-index: -1;
      transform: translateY(-50%);
    }
    &:before {
      left: 0;
      // transform: translate(-50%, -50%);
    }
    &:after {
      right: 0;
      // transform: translate(-50%, 50%);
    }
    & ~ .input-type-range-delimiter {
      position: absolute;
      left: 50%;
      top: 50%;
      right: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 5px;
      z-index: -1;
      &:before,
      &:after {
        position: absolute;
        display: block;
        content: "";
        background: #b7b1b1;
        border-radius: 3px;
        width: 5px;
        height: 5px;
        z-index: -1;
      }
      &:before {
        left: 25%;
        right: 75%;
      }
      &:after {
        left: 75%;
        right: 25%;
      }
      &:nth-child(2) {
        &:before,
        &:after {
          left: 50%;
          right: 50%;
          z-index: -1;
        }
      }
    }
  }
  .input-type-range-thumb-container {
    position: absolute;
    right: 0;
    left: 25px;
    height: 5px;
    transform: translateY(-50%);
    border-bottom-left-radius: inherit;
    border-top-left-radius: inherit;
  }
  .input-type-range-thumb {
    position: relative;
    //top: -50%;
    transform: translateY(-50%);
    &:before,
    &:after {
      content: "";
      position: absolute;
    }
    &:before {
      content: "";
      position: absolute;
      background-color: $main-color;
      height: 5px;
      z-index: -1;
