.arrow {
  &__left,
  &__right,
  &__down {
    border-radius: 1rem;
    box-sizing: border-box;
    background-color: #b7b1b1;
    &:before,
    &:after {
      position: absolute;
      content: "";
      box-sizing: border-box;
    }
  }
  &__left,
  &__right {
    position: absolute;
    height: 2px;
    width: calc(100% - 98px - 50px);
    right: 25px;
    top: 49px;
    @include media-breakpoint-down(sm) {
      display: none;
    }
  }
  &__down {
    position: relative;
    @include media-breakpoint-up(md) {
      display: none;
    }
    left: calc(98px / 2);
    width: 2px;
    height: 48px;
    &:before {
      top: 0;
      left: 50%;
      width: 6px;
      height: 6px;
      border-radius: inherit;
      background-color: #b7b1b1;
      transform: translate(-50%, -50%);
    }
    &:after {
      left: 50%;
      bottom: 0;
      width: 22px;
      height: 22px;
      border-style: none solid solid none;
      border-width: 1px;
      border-color: transparent #b7b1b1 #b7b1b1 transparent;
      transform-origin: left bottom;
      transform: rotate(45deg) translateX(calc(-50% - 11px)) translateY(calc(-50% + 11px));
    }
  }
  &__left {
    &:before {
      left: 0;
      bottom: 0;
      width: 22px;
      height: 22px;
      border-style: none none solid solid;
      border-width: 1px;
      border-color: transparent transparent #b7b1b1 #b7b1b1;
      transform-origin: left bottom;
      transform: rotate(45deg) translate(-1px, 0);
    }
    &:after {
      top: 50%;
      right: 0;
      width: 6px;
      height: 6px;
      border-radius: inherit;
      background-color: #b7b1b1;
      transform: translate(50%, -50%);
    }
  }
  &__right {
    &:before {
      top: 50%;
      left: 0;
      width: 6px;
      height: 6px;
      border-radius: inherit;
      background-color: #b7b1b1;
      transform: translate(-50%, -50%);
    }
    &:after {
      right: 9px;
      bottom: 0;
      width: 22px;
      height: 22px;
      border-style: solid solid none none;
      border-width: 1px;
      border-color: #b7b1b1 #b7b1b1 transparent transparent;
      transform-origin: left bottom;
      transform: rotate(45deg) translate(0, -1px);
    }
  }
  &__left-to-right,
  &__right-to-left {
    position: absolute;
    top: 49px;
    box-sizing: border-box;
    border-style: solid;
    border-width: 2px;
    @include media-breakpoint-down(sm) {
      display: none;
    }
    &:before {
      content: "";
      position: absolute;
      top: 0;
      box-sizing: border-box;
      width: 6px;
      height: 6px;
      border-radius: 1rem;
      background-color: #b7b1b1;
    }
    &:after {
      content: "";
      position: absolute;
      bottom: 0;
    }
  }
  &__right-to-left {
    border-color: #b7b1b1 #b7b1b1 #b7b1b1 transparent;
    border-radius: 0 14px 14px 0;
    width: calc(100% - (98px + 30px));
    left: calc(98px + 30px);

    @include media-breakpoint-only(md) {
      height: calc(221px + 64px);
    }
    @include media-breakpoint-only(lg) {
      height: calc(197px + 64px);
    }
    @include media-breakpoint-up(xl) {
      height: calc(173px + 64px);
    }
    &:before {
      left: 0;
      transform: translate(-50%, -50%);
    }
    &:after {
      left: 0;
      bottom: 0;
      width: 22px;
      height: 22px;
      border-style: none none solid solid;
      border-width: 1px;
      border-color: transparent transparent #b7b1b1 #b7b1b1;
      transform-origin: left bottom;
      transform: rotate(45deg) translate(-1px, 2px);
    }
  }
  &__left-to-right {
    border-radius: 14px 0 0 14px;
    width: calc(15% + 30px);
    transform: translateX(calc(-100% - 30px));
    border-color: #b7b1b1 transparent #b7b1b1 #b7b1b1;
    @include media-breakpoint-only(md) {
      height: calc(269px + 64px);
    }
    @include media-breakpoint-only(lg) {
      height: calc(221px + 64px);
    }
    @include media-breakpoint-up(xl) {
      height: calc(197px + 64px);
    }
    &:before {
      right: 0;
      transform: translate(50%, -50%);
    }
    &:after {
      right: 9px;
      bottom: 0;
      width: 22px;
      height: 22px;
      border-style: solid solid none none;
      border-width: 1px;
      border-color: #b7b1b1 #b7b1b1 transparent transparent;
      transform-origin: left bottom;
      transform: rotate(45deg) translate(2px, -1px);
    }
  }
}

