  height: 100%;
  left: 0;
  top: 0;
  z-index: -1;
  .circle,
  .circle-grad {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    font-size: 0;
    line-height: 0;
    &:before,
    &:after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
  }
  .circle {
    &:before,
