      background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" ><defs><linearGradient id="CircleGradient" x1="0" x2="100" y1="0" y2="0" gradientUnits="userSpaceOnUse" gradientTransform="rotate(-28.96)"><stop offset="0%" style="stop-color:black;stop-opacity:0.11"/><stop offset="36%" style="stop-color:black;stop-opacity:0.24"/><stop offset="100%" style="stop-color:black;stop-opacity:0.05"/></linearGradient></defs><rect x="0" y="0" width="100%" height="100%" style="height:calc(100% - 2px);width:calc(100% - 2px)" rx="1000" ry="1000" stroke-width="1" fill="transparent" stroke="url(%23CircleGradient)"/></svg>');
    }
  }
  > .circle,
  .circle-grad {
    min-width: 376px;
    min-height: 376px;
    @include media-breakpoint-down(xs) {
      min-width: 178px;
      min-height: 178px;
