// Video Modal
.video-modal,
.video-modal .overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 3000;
}

.video-modal {
  overflow: hidden;
  position: fixed;
  transform: translateX(500%);
  transition: transform 0s linear 0s;
  display: flex;
  align-items: center;
  //opacity: 1;
  .overlay {
    z-index: 0;
    opacity: 1;
    background: rgba(0, 0, 0, 0.7);
    transition: opacity 0.2s ease-out 0.05s;
  }
  &.show {
    opacity: 1;
    transform: translateX(0);
    .overlay {
      opacity: 1;
    }
    .video-modal-content {
      transform: translateX(0);
    }
  }
}

.video-modal-content {
  position: relative;
  left: auto;
  right: auto;
  top: auto;
  bottom: auto;
  z-index: 1;
  margin: 0 auto;
  overflow-x: visible;
  background-color: white;
  height: 0;
  width: calc(100% - 12em);
  padding-top: calc((100% - 12em) * 0.5625);
  @media (min-aspect-ratio: 16/9) {
    width: 0;
    height: calc(100vh - 10em);
    padding-top: 0;
    padding-left: calc((100vh - 10em) * 1.7778);
  }
  @include media-breakpoint-down(xs) {
    width: calc(100% - 1em);
    padding-top: calc((100% - 1em) * 0.5625);
  }
}

.close-video-modal {
  display: block;
  position: absolute;
  right: 0;
  top: -40px;
  text-decoration: none;
  width: 25px;
  height: 25px;
  &:before,
  &:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 25px;
    height: 2px;
    background: white;
  }
  &:before {
    transform: rotate(45deg);
  }
  &:after {
    transform: rotate(-45deg);
  }
}

