$main-color: rgb(45, 97, 206);
$border-color: rgb(77, 117, 255);

$orange-color: rgb(255, 192, 1);

$paragraph-color: rgb(102, 102, 102);
$text-color: rgba(0, 0, 0, 0.87);

$gen-grad-col1: rgba(235, 237, 239, 1);
$gen-grad-col2: rgba(209, 212, 216, 1);

// Headings
h1,
.h1 {
  @include adaptive-font-size(48, 32);
  @include adaptive-line-height(46, 30);
  letter-spacing: 1.33px;
  font-weight: normal;
}
h2,
.h2 {
  @include adaptive-font-size(48, 32);
  @include adaptive-line-height(46, 30);
  letter-spacing: 1.33px;
  font-weight: normal;
}
h3,
.h3 {
  @include adaptive-font-size(32, 20);
  @include adaptive-line-height(30, 30);
  letter-spacing: 1.33px;
  font-weight: normal;
}
h4,
.h4 {
  @include adaptive-font-size(26, 26);
  @include adaptive-line-height(34, 34);
  letter-spacing: 1.86px;
  font-weight: 500;
}
h5,
.h5 {
  @include adaptive-font-size(18, 18);
  @include adaptive-line-height(24, 24);
  letter-spacing: 0.75px;
  font-weight: 700;
}
h6,
.h6 {
  @include adaptive-font-size(15, 15);
  @include adaptive-line-height(24, 24);
  font-weight: 700;
}

p {
  .text {
    color: $paragraph-color;
  }
}

// Buttons
.btn {
  white-space: nowrap;
  //padding: 0.5rem 1rem;
  padding: 1rem 1.5rem;
  letter-spacing: 1px;
  font-weight: 600;
  font-size: 0.875rem;
  &:focus {
    outline: none;
    box-shadow: none;
  }
}

.btn-md {
  white-space: nowrap;
  padding: 1rem 2rem;
  letter-spacing: 1px;
  font-weight: 700;
}

.btn-xl {
  padding: 1.375rem 3.25rem;
  font-size: 1.375rem;
  line-height: 1.375;
  letter-spacing: 1.22px;
  font-weight: 700;
  @include media-breakpoint-down(xs) {
    padding: 1.375rem 2.2rem;
  }
}

.btn-general {
  border: 2px solid $border-color;
  border: 1px solid $main-color;
  background-color: $main-color;
  color: white;
  &:hover,
  &:focus {
    color: white;
    background-color: darken($color: $main-color, $amount: 10%);
    border-color: darken($color: $main-color, $amount: 10%);
  }
}

.btn-outline {
  border: 1.5px solid black;
  color: $text-color;
  &:hover {
    color: white;
    background-color: $text-color;
    .right-arrow,
    .left-arrow {
      background-color: white;
      &:before {
        border-color: white;
      }
    }
  }
}

.btn-rounded {
  border-radius: 5rem;
}

.btn-shadow {
  box-shadow: inset 0 0 1px rgba(255, 255, 255, 1), 0 15px 16px -13px rgba(0, 0, 0, 0.26), 0 2px 1px -1px rgba(0, 0, 0, 0.15);
}

.icon-oval {
  width: 72px;
  height: 72px;
  border-radius: 36px;
  background-color: $main-color;
  text-align: center;
  line-height: 72px;
}

.icon-circle {
  position: relative;
  &:after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 98px;
    height: 98px;
    background: url('data:image/svg+xml;utf8,<svg   xmlns="http://www.w3.org/2000/svg" ><defs><linearGradient id="OrangeGradient" x1="0" x2="100" y1="0" y2="0" gradientUnits="userSpaceOnUse"><stop stop-color="rgba(112, 164, 255, 0.43)" offset="0"/><stop stop-color="rgb(76, 154, 251)" offset="1"/></linearGradient></defs><rect x="2" y="2" width="100%" height="100%" style="height:calc(100% - 4px);width:calc(100% - 4px)" rx="49" ry="49" stroke-width="2" fill="transparent" transform="rotate(133 49 49)" stroke="url(%23OrangeGradient)"/></svg>');
  }
}

// Background Gradients
.bg-gradient {
  position: relative;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    right: -50vw;
    left: 0;
    bottom: 0;
    z-index: -1;
    background: linear-gradient(135deg, #ebedef 0%, #d1d4d8 100%);
  }
}
